.portfolio {
    text-align: center;
    margin-bottom: 20px;
    font-size: 36px;
    color: #ffffff;

}

.Par {
    color: #ffffff;

}

.separator {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
}

.double-dash {
    font-size: 24px;
    color: #ffffff5e;
    padding: 0 10px;
}

.first-item {
    background-color: rgba(53, 53, 53, 0.47);
    backdrop-filter: blur(62px);
}

.vertical-timeline-element-image {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.vertical-timeline-element-image img {
    width: 30%;
    height: auto;
    object-fit: cover;
    margin-right: 5px;
}