/* Testimonials Section */
#testimonial {
    padding-top: 100px;
    padding-bottom: 100px;
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(15px);
    margin-left: 30px;
    margin-right: 30px;
    border-radius: 15px;
}

.testimonial-line {
    display: flex;
    list-style-type: none;
    margin-left: -15px;
    margin-right: -15px;
}

.testimonial-line li {
    width: 25%;
    height: 5px;
    margin: 0 15px;
    background-color: #ddd;
}

.testimonial-line li.active {
    background-color: #555;
}

.testimonial-quote {
    font-size: 4rem;
    color: #4caf50;
    margin-bottom: 40px;
}

.testimonial-slide {
    position: relative;
    padding: 40px;
    text-align: center;
    background-color: #ffffff9d;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);
    border-radius: 10px;
}

.testimonial-slide:before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 0;
    border-top: 10px solid #ffffff9d;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid transparent;
}

.testimonial-slide:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 0;
    border-bottom: 10px solid #ffffff9d;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-top: 10px solid transparent;
}

.testimonial-slide .author-info {
    margin-top: 20px;
}

.testimonial-slide .author-name {
    font-weight: bold;
}

.testimonial-slide .author-designation {
    text-transform: uppercase;
    font-weight: bold;
    color: #999;
}

.testimonial-slide .star {
    list-style: none;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 30px;
}

.testimonial-slide .star li {
    margin-right: 5px;
}

.testimonial-slide .star li:last-child {
    margin-right: 0;
}

.testimonial-slide .star svg {
    color: #ffc107;
    font-size: 1.2rem;
}

.testimonial-slide .review {
    color: #999;
    font-size: 0.9rem;
}

@media (max-width: 1024px) {
    .testimonial-slide {
        padding: 20px;
    }
}

@media (max-width: 767px) {

    .testimonial-slide:before,
    .testimonial-slide:after {
        display: none;
    }
}