.hero {
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 80px 0;
    position: relative;
    z-index: 0;
}

.hero-icons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}

.hero-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 60px;
    margin: 0 2rem;
    border-radius: 80%;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: all 0.3s ease-out;
}

.hero-icon:hover {
    transform: translateY(-10px);
    box-shadow: 0px 8px 16px rgba(0, 115, 247, 0.25);
}

.hero-text {
    font-size: 3rem;
    text-align: center;
    margin-bottom: 1rem;
    position: relative;
}


.hero-text span {
    color: #0289d8;
}

.hero-sub-text {
    font-size: 1.5rem;
    text-align: center;
    margin-bottom: 2rem;
}

.hero-btns {
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-btn {
    display: inline-block;
    background-color: transparent;
    color: #fff;
    font-size: 1.2rem;
    text-align: center;
    padding: 7px 14px;
    border: 2px solid #fff;
    border-radius: 2rem;
    transition: all 0.3s ease-out;
    box-shadow: 0px 4px 4px #a4afc4;
    margin: 0 1rem;
}

.hero-btn:hover {
    background-color: #fff;
    color: #102A5E;
    box-shadow: 0px 8px 16px rgba(0, 115, 247, 0.25);
    transform: translateY(-3px);
}

.hero-btn:active {
    background-color: #102A5E;
    color: #102A5E;
    box-shadow: rgba(0, 115, 247, 0.25);
    transform: translateY(0);
}