/* Style for the services container */
.services-container {
    display: contents;
    margin-top: 60px;
}

/* Style for the services title */
.services-container h2 {
    text-align: center;
    margin-bottom: 40px;
    font-size: 36px;
    color: #ffffff;
}

.separator {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 50px 0;
}

.double-dash {
    font-size: 24px;
    color: #ffffff5e;
    padding: 0 10px;
}